import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Tabs,
  Modal
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
const {TabPane} = Tabs
import {API_PACKAGE_DELIVERYORDER_IMPORT} from 'api'
import {Import} from 'HOC'
const confirm = Modal.confirm

@connect(
  state => ({...state.packageList}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
      submitData: {}
    }
  }
  changeState = (type) => {
    const {selectedRows, handleClose} = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }
    let orderList = []
    selectedRows.map((o) => {
      orderList.push(o.id)
    })
    handleClose({data: orderList}, type, (data) => {
      if (type == 1) {
        message.success('')
      } else {
        confirm({
          title: '',
          content: data.result,
          cancelText: '',
          okText: '',
          onOk() {
          },
          onCancel() {}
        })
      }
      const {handleGetList, searchParams, page, size} = this.props
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }

  /*  */
  handleBrowse = (type) => {
    const {selectedRows, handlePackageJudge} = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }

    //  
    if (type == 1) {
      // 
      let pass = true
      let num = ''
      for (let i = 0; i < selectedRows.length; i++) {
        if (i == 0) {
          num = selectedRows[i].waveCode
        } else {
          if (selectedRows[i].waveCode != num) {
            pass = false
            break
          }
        }
      }
      if (!pass) {
        message.error('')
        return
      }
      let arr = []
      selectedRows.map((o, i) => arr.push(o.orderCode))
      handlePackageJudge({data: {packageCode: arr.toString()}}, (data) => {
        this.newOrOld(data, type)
      })
      return
    }
    let orderList = [],
      orderNum = []
    selectedRows.map((o) => {
      orderList.push(o.id)
      orderNum.push(o.orderCode)
    })
    setVariant('wms/packageList', orderList)
    setVariant('wms/packageListNum', orderNum)
    window.open(`/wms/packageList/Print/${type}`, 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  // 
  newOrOld(data, type) {
    const {businessTypeCode, packageId, packageCode} = data.result
    const {selectedRows} = this.props
    // 
    if (businessTypeCode == 22) {
      this.setState({
        submitData: data.result
      }, () => {
        this.form.submit()
      })
    } else {
      let orderList = [],
        orderNum = []
      selectedRows.map((o) => {
        orderList.push(o.id)
        orderNum.push(o.orderCode)
      })
      setVariant('wms/packageList', orderList)
      setVariant('wms/packageListNum', orderNum)
      window.open(`/wms/packageList/Print/${type}`, 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
    }
  }
    // 
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, status: tableActiveKey, size}}, true)
  }

    /*  */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

    /*  */
  handleImportSuccess = res => {
    const {handleGetList, searchParams, page, size} = this.props
    res && res.message === 'ok' && message.success('')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /*  */
  handleExprotError = () => {
  }

  // 
  handlePackage = (type) => {
    const {selectedRows, handlePackage} = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }
    let orderList = []
    selectedRows.map((o) => {
      orderList.push(o.orderCode)
    })
    handlePackage({data: {packageCodeList: orderList}}, type, () => {
      message.success('')
      const {handleGetList, searchParams, page, size} = this.props
      handleGetList({searchParams: {page, size, ...searchParams}})
    })
  }
  /*  */
  handleBrowse2 = (type) => {
    let {selectedRows} = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }
    let arr = []
    selectedRows.map((o, i) => arr.push({
      orderCode: o.outCode,
      packageCode: o.orderCode
    }))
    arr = [...new Set(arr)]
    setVariant('wms/outWarehouseListWave', arr)
    window.open(`/wms/outWarehouseListWave/Print/${type}`, 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, tab4} = this.props
    const {submitData} = this.state
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse.bind(this, 1)}></Button>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse.bind(this, 2)}></Button>
        <Button type="primary" className={style.btn} onClick={this.changeState.bind(this, 1)}></Button>
        <Button type="primary" className={style.btn} onClick={this.changeState.bind(this, 2)}></Button>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T06')
        }}></Button>
        <Import
          action={{api: API_PACKAGE_DELIVERYORDER_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}></Button>
        </Import>

        {
          (tableActiveKey == '0' || tableActiveKey == '4') && <Button type="primary" className={style.btn} onClick={this.handlePackage.bind(this, 1)}></Button>
        }
        {
          (tableActiveKey == '0' || tableActiveKey == '4') && <Button type="primary" className={style.btn} onClick={this.handlePackage.bind(this, 2)}></Button>
        }

        <Button type="primary" className={style.btn} onClick={this.handleBrowse2.bind(this, 1)}></Button>
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`(${tab4 || 0})`} key="4"></TabPane>
          <TabPane tab={`(${tab3 || 0})`} key="3"></TabPane>
          <TabPane tab={`(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`(${tab2 || 0})`} key="2"></TabPane>
        </Tabs>
        <form style={{display: 'none'}} ref={el => this.form = el} action="http://www.kdniao.com/External/PrintOrder.aspx" method="post" target="_blank">
          <div>
            <div><input type="text" id="RequestData" name="RequestData" value={encodeURI(submitData.requestData)} onChange={() => {}}/></div>
            <div><input type="text" id="EBusinessID" name="EBusinessID" value={submitData.ebusinessID} onChange={() => {}}/></div>
            <div><input type="text" id="DataSign" name="DataSign" value={submitData.dataSign} onChange={() => {}}/></div>
            <div><input type="text" id="IsPreview" name="IsPreview" value={submitData.isPreview} onChange={() => {}}/></div>
          </div>
        </form>
      </div>
    )
  }
}



