import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  Checkbox
} from 'antd'
import style from './style/Form.scss'
import {API_GETPACKAGESTATUS_KEYVALUES, API_GETEXPRESSINFO_KEYVALUES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect} from 'Form'

@connect(
  state => ({...state.packageList, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }

  // 
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {
        // status,
        isPrint,
        deliveryName,
        isNull,
        orderCode
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label></label>
              <div className='box'>
                <Input placeholder='' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_GETEXPRESSINFO_KEYVALUES}
              label=''
              selectItem={{data: deliveryName, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({deliveryName: value})
              }}
            />
          </Col>
          {/* <Col span={6}>
            <CsxSelect
              dateApi={API_GETPACKAGESTATUS_KEYVALUES}
              label=''
              selectItem={{data: status, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({status: value})
              }}
            />
          </Col> */}
          <Col span={6}>
            <Checkbox style={{marginLeft: 25}} checked={isPrint ? true : false} onChange={(e) => {
              this.onInputChange({isPrint: e.target.checked})
            }}></Checkbox>
            <Checkbox checked={isNull ? true : false} onChange={(e) => {
              this.onInputChange({isNull: e.target.checked})
            }}></Checkbox>
          </Col>
          <Col span={6}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} ></Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} ></Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



