import React, {PureComponent} from 'react'
import {
  Table,
  Icon,
  Popover
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({...state.packageList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <React.Fragment><a onClick={() => {
        const {history: {location}} = this.props
        window.open(`${location.pathname}/${record.orderCode}`)
        // history.push(`${location.pathname}/${record.orderCode}`)
      }}>{text}</a><Popover content={<img id={`key${text}`} style={{width: '237px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '',
      width: 150,
      dataIndex: 'outCode',
      key: 'outCode'
    }, {
      title: '',
      width: 180,
      dataIndex: 'waveCode',
      key: 'waveCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{width: '253px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '',
      width: 100,
      dataIndex: 'statusCn',
      key: 'statusCn'
    }, {
      title: '',
      width: 100,
      dataIndex: 'deliveryName',
      key: 'deliveryName'
    }, {
      title: '',
      width: 100,
      dataIndex: 'deliveryOrderCode',
      key: 'deliveryOrderCode',
      render: (text, record, index) => text && <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{width: '287px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '',
      width: 100,
      dataIndex: 'printTimes',
      key: 'printTimes'
    }, {
      title: '',
      width: 150,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '',
      width: 150,
      dataIndex: 'sendTime',
      key: 'sendTime'
    }, {
      title: '',
      width: 150,
      dataIndex: 'updateBy',
      key: 'updateBy'
    }]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }

  // 
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }

  //
  onEdit = (modalType, record) => {
    const {update} = this.props
    update({[modalType]: true, modalId: [record.id]})
  }

  //  columns
  // isAsc  true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field}}, true)
      } else {
        handleGetList({searchParams: {page, size, status: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field}}, true)
      }
    } else {
      // 
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, status: tableActiveKey, ...searchParams}}, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => ` ${total} `, // 
      showQuickJumper: true, // 
      current: page,
      pageSize: size,
      showSizeChanger: true, //  pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



